import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { createPlaylist } from '../utils/hooks';
import { TimeDisplay } from '../TimeDisplay';
import styles from './player.scss';
import { circleData, canvasWidth, mainRadius, pointsLine, pointsTriangle } from '../geometry.const';

const MAX_CHUNKS = 3;
const INITIAL_CHUNK = 1;


const pauseBar1X = 242;
const pauseBar1Y = 392;
const pauseBar2X = pauseBar1X + 10;

const playPoint1 = `240,${pauseBar1Y}`;
const playPoint2 = '240,418';
const playPoint3 = '262,405';


const chapterInfo = {
  1: {
    name: 'Act I',
    duration: 10
  },
  2: {
    name: 'Act II',
    duration: 10
  },
  3: {
    name: 'Act III',
    duration: 10
  },
  4: {
    name: 'Epilogue',
    duration: 10
  }
};


const formatTime = (time) => {
  let t = time;
  let hour;
  let strTime = '';
  if (t > 3600) {
    hour = Math.floor(t / 3600);
    t -= (hour * 3600);
    strTime = hour === 0 ? '' : `${hour}:`;
  }
  let minute = 0;
  if (t > 60) {
    minute = Math.floor(t / 60);
    t -= (minute * 60);
    strTime = `${strTime}${minute < 10 ? `0${minute}` : minute}`;
  } else {
    strTime = `${strTime}00`;
  }

  const seconds = Math.floor(t);
  return `${strTime}:${seconds < 10 ? `0${seconds}` : seconds}`;
};

const Player = ({ speakers }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [speakerIndex, setSpeakerIndex] = useState(/** @type number */ undefined);
  const [chapter, setChapter] = useState(INITIAL_CHUNK);
  const [timeOffset, setTimeOffset] = useState(0);
  const [time, setTime] = useState(0);
  const [streams, setStreams] = useState(/** @key Object<Howl[]>*/ undefined);
  const [textOpacity, setTextOpacity] = useState(0);
  const [displayName, setDisplayName] = useState('');
  const [currentStream, setCurrentStream] = useState(undefined);
  const [isBuffering, setIsBuffering] = useState(true);

  /**
   * stream management
   */
  useEffect(() => {
    const s = {};
    speakers.forEach((speaker, index) => {
      s[index] = createPlaylist(speaker.track, 3, (id) => {
        if (index === 0) {
          // update duration to the correct thing
          chapterInfo[id].duration = s[index][id].stream.duration();
        }
      }, () => {
        setChapter((prevState) => {
          if (prevState === MAX_CHUNKS) {
            console.log('ended');
            setTime(0);
            setSpeakerIndex(undefined);
            return INITIAL_CHUNK;
          }
          console.log('next chunk');
          return prevState + 1;
        });
      });
    });

    setStreams(s);
  }, [speakers]);

  /**
   * preloading
   */
  useEffect(() => {
    if (streams === undefined) return;
    if (speakerIndex === undefined) {
      Object.keys(streams).forEach((key) => {
        streams[key][INITIAL_CHUNK].init();
      });
    }

    // load the next stream
    const nextChunk = chapter + 1;
    if (nextChunk <= MAX_CHUNKS) {
      Object.keys(streams).forEach((key) => {
        streams[key][nextChunk].init();
      });
    }
  }, [streams, chapter]);

  /**
   * speaker switching
   */
  useEffect(() => {
    if (speakerIndex === undefined) {
      console.log('reset EVERYTHING');
      // reset everything
      setIsPlaying(false);
      setTime(0);
      setTimeOffset(0);
      setCurrentStream(undefined);
      return;
    }
    if (streams && streams[speakerIndex] && streams[speakerIndex][chapter]) {
      setCurrentStream(streams[speakerIndex][chapter].stream);
    }
  }, [speakerIndex, streams, chapter]);

  /**
   * chapter switching
   */
  useEffect(() => {
    if (chapter) {
      setTime(0);
      if (chapter > INITIAL_CHUNK) {
        setTimeOffset((prevTimeOffset) => prevTimeOffset + chapterInfo[chapter - 1].duration);
      }
    }
  }, [chapter]);

  /**
   * Buffering
   */
  useEffect(() => {
    if (isBuffering && currentStream) {
      let t = setInterval(() => {
        if (currentStream.state() === 'loaded') {
          setIsBuffering(false);
          clearInterval(t);
          t = undefined;
        } else {
          setIsBuffering(true);
        }
      }, 100);
      return () => {
        if (t) {
          clearInterval(t);
        }
      };
    }
    return null;
  }, [isBuffering, currentStream]);


  useEffect(() => {
    if (!speakers[speakerIndex]) {
      setDisplayName('');
      return undefined;
    }
    setTextOpacity(0);
    const t = setTimeout(() => {
      setTextOpacity(1);
      setDisplayName(speakers[speakerIndex].title);
    }, 1000);

    return () => {
      clearTimeout(t);
    };
  }, [speakerIndex]);


  /**
   * handle player control
   */
  useEffect(() => {
    if (streams === undefined || speakerIndex === undefined) return undefined;
    let t;
    if (isPlaying) {
      currentStream.seek(time);
      currentStream.play();
      t = setInterval(() => {
        setTime(currentStream.seek());
      }, 100);
    } else {
      currentStream.pause();
    }

    return () => {
      currentStream.stop();
      if (t) {
        clearInterval(t);
      }
    };
  }, [isPlaying, currentStream]);

  // return <TimeDisplay time={time} />;

  return (
    <>
      <svg width={canvasWidth} height={canvasWidth + 200} className={styles.logo}>
        <circle cx={canvasWidth / 2} cy={canvasWidth / 2} r={mainRadius} />
        <polyline points={pointsTriangle} />
        <polyline points={pointsLine} />
        {
          circleData.map(({ x, y, r, className }, index) => {
            if (index === (circleData.length - 1)) {
              return (
                <circle
                  key={`${x},${y}`}
                  cx={x}
                  cy={y}
                  r={r}
                  className={styles.circle}
                />
              );
            }
            return (
              <circle
                role="button"
                key={`${x},${y}`}
                tabIndex="0"
                cx={x}
                cy={y}
                r={r}
                className={speakerIndex === index ? styles.circleSelected : styles.circleSelectable}
                onClick={() => {
                  setSpeakerIndex(index);
                }}
                onKeyDown={(e) => {
                  // spacebar or return
                  if (e.keyCode === 32 || e.keyCode === 13) {
                    e.preventDefault();
                    setSpeakerIndex(index);
                  }
                }}
              />
            );
          })
        }
        <text x="50%" y="45%" style={{ opacity: textOpacity }}>
          {displayName}
        </text>
        <text x="50%" y="50%" style={{ opacity: speakerIndex === undefined ? 0 : 1 }}>
          {chapter ? chapterInfo[chapter].name : undefined}
          <tspan x="50%" y="53%" className={styles.buffering}>
            {isBuffering ? 'buffering...' : formatTime(time + timeOffset)}
          </tspan>
        </text>
        <g
          className={styles.controls}
          role="button"
          tabIndex="0"
          onClick={() => {
            setIsPlaying((prev) => !prev);
          }}
          style={{ opacity: speakerIndex === undefined ? 0 : 1 }}
        >
          <rect x={230} y="55%" width={40} height={40} rx={5} ry={5} />
          <g className={styles.controlIcon}>
            {
              isPlaying
                ? (
                  <g>
                    <rect x={pauseBar1X} y={pauseBar1Y} width={5} height={25} rx={2} ry={2} />
                    <rect x={pauseBar2X} y={pauseBar1Y} width={5} height={25} rx={2} ry={2} />
                  </g>
                )
                : <polygon points={`${playPoint1} ${playPoint2} ${playPoint3} ${playPoint1}`} />

            }
          </g>
        </g>
        Sorry, your browser does not support inline SVG.
      </svg>
      {
        __DEVELOPMENT__ ? (
          <>
            <TimeDisplay time={time} />
            <TimeDisplay time={time + timeOffset} />
          </>
        ) : undefined
      }
    </>
  );
};

Player.propTypes = {
  speakers: PropTypes.arrayOf(PropTypes.shape({
    title: PropTypes.string,
    track: PropTypes.string
  }))
};

Player.defaultProps = {
  speakers: []
};

export {
  Player
};
