export const canvasWidth = 500;
export const mainRadius = 450 / 2;
const radius = 20;

const point1x = 45;
const point1y = 340;

const point2x = canvasWidth - point1x;

const point3x = canvasWidth / 2;
const point3y = 25;

const point4y = canvasWidth - point3y;
const point5y = point4y + 190;


export const pointsTriangle = `${point1x}, ${point1y} ${point2x}, ${point1y} ${point3x},${point3y} ${point1x}, ${point1y}`; // eslint-disable-line max-len
export const pointsLine = `${point3x}, ${point4y} ${point3x}, ${point5y} `;


export const circleData = [
  {
    x: point1x,
    y: point1y,
    r: radius
  },
  {
    x: point3x,
    y: point3y,
    r: radius
  },
  {
    x: point2x,
    y: point1y,
    r: radius
  },
  {
    x: point3x,
    y: point4y,
    r: radius
  },
  {
    x: point3x,
    y: point5y,
    r: radius
  }
];
