      + '/_/ /_/ /_/\\___/  /___/\\__, /_/ /_/_/\\__/_/\\____/_/ /_/  (_)  \n'
      + '    ____             _/____/                                  \n'
      + '   / __ \\___  ____ _/ /___ ___                                \n'
      + '  / /_/ / _ \\/ __ `/ / __ `__ \\                               \n'
      + ' / _, _/  __/ /_/ / / / / / / /                               \n'
      + '/_/ |_|\\___/\\__,_/_/_/ /_/_/_/__                              \n'
      + '            ____/ /____(_) __/ /____  _____                   \n'
      + '           / __  / ___/ / /_/ __/ _ \\/ ___/                   \n'
      + '          / /_/ / /  / / __/ /_/  __/ /                       \n'
