/**
 *
 * @param {string} name
 * @param {number} totalChunks
 * @param {function} onend
 * @return {any[]}
 */
export const createPlaylist = (name, totalChunks = 3, onload, onend) => {
  const streams = Array(totalChunks);

  for (let i = 1; i <= totalChunks; i += 1) {
    streams[i] = {};
    streams[i].init = function () {
      if (streams[i].stream === undefined) {
        streams[i].stream = new howler.Howl({
          src: __USE_SMALL__
            ? [
              `assets/audio/${name}.act${i}.mp3`
            ]
            : [
              `assets/audio/${name}.act${i}.webm`,
              `assets/audio/${name}.act${i}.mp3`
            ],
          autoUnlock: true,
          preload: true,
          html5: true, // needed for streaming
          html5PoolSize: 20,
          onend,
          onload: () => {
            console.log(`loaded ${name} part ${i}`);
            onload(i);
          },
          onloaderror: (e) => {
            console.error(name);
            console.error(e);
          },
          onplayerror: (e) => {
            console.error(name);
            console.error(e);
          }
        });
      }
    };
  }

  return streams;
};
